<template>
  <view class="site-activities">
    <view class="des-content-title">站点活动</view>
    <view v-if="activityList.length" class="activity-list">
      <view v-for="(item, index) in activityList" :key="index" class="activity-item">
        <ActivityItem :item="item" multipleSize="180rpx" />
      </view>
    </view>
    <view v-else class="empty-tip">
      <empty />
    </view>
  </view>
</template>

<script>
import ActivityItem from '@/components/ActivityItem/index.vue'
import { getVoluntaryActivityList } from '@/api/instructorSite/index'
export default {
  name: 'SiteActivities',
  components: {
    ActivityItem,
  },
  props: {
    siteId: {
      type: [String, Number],
      required: true,
    },
  },
  data() {
    return {
      activityList: [],
    }
  },
  created() {
    this.getActivityList()
  },
  methods: {
    async getActivityList() {
      let params = {
        page: 1,
        size: 999,
        sort: this.sort,
        instructor_site_id: this.siteId,
      }
      let res = await getVoluntaryActivityList(params)
      if (res.code !== 200) {
        this.$toast(res.message)
        return
      }
      res.data.list.map((item) => {
        let formatObj = {
          addressFormat: item.province_str + item.county_str + item.city_str + item.address,
          startTime: this.$dayjs(item.c_time * 1000).format('YYYY-MM-DD HH:mm:ss'),
        }
        return {
          ...formatObj,
          ...item,
        }
      })
      console.log('[ res ] >', res)
      this.activityList = res.data.list
    },
  },
}
</script>

